@import "animation";
@import "font";
@import "mixin";
@import "base";
body{
  background-color: $lightGray !important;
}
.header {
  width: 100%;
  height: 90px;
  padding: 20px 0;
  background-color: $mainColor;


  .contactMenu {
    ul {
      @include flexLayout();
      justify-content: space-between;
      width: 200px;
      padding: 0;

      li {
        padding: 5px 10px;
        @include myborder;

        padding-right: 0;

        a {
          @include linkDecoration;
          @include flexcolumn;

        }

      }


    }

  }

  nav {

    ul {
      @include flexLayout;

      li {
        padding: 0 10px;
        padding-right: 0;


        ul {
          @include flexLayout;
          justify-content: space-between;

          li {
            padding: 5px 10px;
            @include myborder;

            a {
              @include flexcolumn;

              @include linkDecoration;

            }
          }


        }
      }
    }
  }

}


// end Header
// start searchFrame
.search-frame {
  color: #0092f7;
  background-image: url("../img/uzVDNJUi1w.png");
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 600px;
  border-radius: 0 0 20px 20px;



  .motto {
    color: $mainColor;
    font-family: $font-main;
    margin-bottom: 40px;


  }

  .search-box {
    display: flex;
    background-color: $lightGray;
    border-radius: 10px;
    height: 50px;
    width: 600px;


    .search-box__title {

    }

    .search-box__category {

    }

    .search-box__btn {
      background-color: #ef2a18;
      border-radius: 10px 0 0 10px;
      width: 60px;
      height: inherit;
      display: flex;
      justify-content: center;
      padding-top: 14px;
      cursor: pointer;

      i {
        font-size: 20px;
        color: $mainColor;
      }

    }

  }


}

// start categoryFrame
.category {
  position: relative;
  top: -100px;
  display: flex;
  justify-content: center;
  .categoryFrame {
    @include flexLayout;
    min-height: 270px;
    background-image: url("");
    border-radius: 15px;
    background-color: $mainColor;
    width: 80%;


    .categoryFrame__product {
      width: 60%;
      padding-right: 30px;

      .categoryFrame__product__title {
        @include mytitle;
        color: $darkColor;
        border-bottom: 2px solid $textColor;

      }

      .categoryFrame__product__menu {
        @include flexLayout;
        justify-content: center;

        .categoryFrame__product__menu__item{
          width: 25%;

          .categoryFrame__product__menu__item_1{
            @include text;
            border-right: 2px solid #ef2a18;
            padding-right: 5px;
            width: 100%;
            margin: 30px 0;
            line-height: 1;
            a{

              @include linkDecoration;
              color: $darkColor;


            }


          }

        }

      }


    }

    .categoryFrame__image {
      width: 40%;
    }

  }
}