$mainColor:white;
$textColor: #828282;
$darkColor: #2b2a2a;
$lightGray:#dcdcdc;

a{
  text-decoration: none;
}
@mixin flexLayout{
  display: flex;
  flex-direction: row-reverse;
  list-style: none;
}
@mixin flexcolumn{
  display: flex;
  flex-direction: column;
  list-style: none;
  align-items: center;
  justify-content: center;
}
@mixin myborder{
  border-left: 1px dashed #a8a4a4;
  padding-bottom: 0;
}

@mixin linkDecoration{
  font-size: 12px;
  text-decoration: none;
  cursor: pointer;
  color: $textColor;
  font-family: $font-main;
}
@mixin text{
  text-align: right;
  font-family: $font-main;
  font-size: 12px;
}
@mixin mytitle{
  line-height: 4;
  text-align: right;
  font-family: $font-main;
  font-size: 14px;
}

.header__brand{
  @include flexLayout();
  justify-content:space-between;


}
.navbar__brand__title{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 12px;

  span{
    font-family: $font-main;
    text-align: center;
    line-height: 1;

  }

}
element {
  border-width: 1px;
  border-style: solid;
  color: rgb(0, 146, 247);
  display: inline-block;
  height: 18px;
  border-radius: 30px;
  vertical-align: middle;
}